import { BrowserRouter, Routes, Route } from "react-router-dom";
import IndexPage from "./app/components/pages/IndexPage";
import AppLoader from "./app/components/layouts/appLoader";
import CreateCommentPage from "./app/components/pages/CreateCommentPage";
import ViewAnnouncePage from "./app/components/pages/ViewAnnouncePage";
import AuthPage from "./app/components/pages/AuthPage";

function App() {
  return (
    <>
      <AppLoader>
        <BrowserRouter>
          <Routes>
            <Route path="/" element={<IndexPage />} />
            <Route path="/auth" element={<AuthPage />} />
            <Route path="/create" element={<CreateCommentPage />} />
            <Route path="/view/:id" element={<ViewAnnouncePage />} />
          </Routes>
        </BrowserRouter>
      </AppLoader>
    </>
  );
}

export default App;
