import { useNavigate } from "react-router-dom";
import ModalCreate from "./ModalCreate";
import ReplyComment from "./Reply";
import * as React from "react";
import { Helmet } from "react-helmet-async";

interface Props {
  announce: any;
  replies: any;
}

const ViewAnnounce: React.FC<Props> = ({ announce, replies }) => {
  const [showModal, setShowModal] = React.useState(" hidden");
  const navigate = useNavigate();
  const getRate = () => {
    const rateList = replies.map((i: any) => i.rate);
    const sumRates = rateList.reduce((acc: any, i: any) => {
      return (acc += i);
    }, 0);
    console.log(Math.floor(sumRates / rateList.length));
    return Math.floor(sumRates / rateList.length);
  };

  const renderStars = () => {
    const starList = [];
    for (let i = 1; i <= getRate(); i++) {
      starList.push("text-yellow-400");
    }
    for (let i = 1; i <= 5 - getRate(); i++) {
      starList.push("text-gray-300");
    }
    return starList;
  };
  const renderCurrentStars = (rate: number) => {
    const starList = [];
    for (let i = 1; i <= rate; i++) {
      starList.push("text-yellow-400");
    }
    for (let i = 1; i <= 5 - rate; i++) {
      starList.push("text-gray-300");
    }
    return starList;
  };
  return (
    <>
      <Helmet>
        <title>{announce.name}</title>
        <meta
          name="description"
          content={`ИНН: ${announce.inn} Адрес: ${announce.adres} Телефон: ${announce.phone}`}
        />
      </Helmet>

      <ModalCreate
        showModal={showModal}
        setShowModal={setShowModal}
        announce={announce}
      />
      <div className="mt-10">
        <h1 className="text-3xl">{announce.name}</h1>
      </div>
      <div className="mt-5 flex gap-x-14 flex-wrap gap-y-5">
        <div className="flex flex-col">
          <p className="text-gray-400 text-sm">Общий рейтинг:</p>
          <div className="flex mt-2">
            {renderStars().map((s) => (
              <svg
                aria-hidden="true"
                className={"w-5 h-5 " + s}
                fill="currentColor"
                viewBox="0 0 20 20"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path d="M9.049 2.927c.3-.921 1.603-.921 1.902 0l1.07 3.292a1 1 0 00.95.69h3.462c.969 0 1.371 1.24.588 1.81l-2.8 2.034a1 1 0 00-.364 1.118l1.07 3.292c.3.921-.755 1.688-1.54 1.118l-2.8-2.034a1 1 0 00-1.175 0l-2.8 2.034c-.784.57-1.838-.197-1.539-1.118l1.07-3.292a1 1 0 00-.364-1.118L2.98 8.72c-.783-.57-.38-1.81.588-1.81h3.461a1 1 0 00.951-.69l1.07-3.292z"></path>
              </svg>
            ))}
            <p className="ml-1.5 text-md text-gray-800">
              {getRate()},0{" "}
              <span className="text-gray-400">({announce.items.length})</span>
            </p>
          </div>
          <div className="mt-2 h-1 bg-yellow-400 w-1/3"></div>
        </div>

        <div className="flex flex-col">
          <p className="text-md font-medium">Информация:</p>
          <p className="text-sm mt-4 font-medium">ИНН: {announce.inn}</p>
          <p className="text-sm mt-2 font-medium">Адрес: {announce.adres}</p>
          <p className="text-sm mt-2 font-medium">Телефон: {announce.phone}</p>
        </div>
      </div>
      <div className="mt-2">
        <h2 className="text-2xl mt-8 pb-4 border-b-2 border-yellow-400 w-fit">
          Отзывы
        </h2>
      </div>
      <div className="border-b w-2/4 border-gray-300"></div>
      <div className="flex flex-col w-full md:w-2/4">
        {replies.map((r: any) => (
          <ReplyComment stars={renderCurrentStars(r.rate)} reply={r} />
        ))}
      </div>
      <div className="border-b w-2/4 border-gray-300 mt-8">
        <div className="border-b-2 border-yellow-400 w-24"></div>
      </div>
      <button
        onClick={() => setShowModal("")}
        className="pl-2 pr-3 py-2 bg-green-400 rounded-sm shadow-md text-white font-normal flex flex-row justify-center items-center h-fit mt-6"
      >
        <svg
          className="w-6 h-6 mr-1"
          fill="none"
          stroke="currentColor"
          viewBox="0 0 24 24"
          xmlns="http://www.w3.org/2000/svg"
        >
          <path
            strokeLinecap="round"
            strokeLinejoin="round"
            strokeWidth={2}
            d="M12 6v6m0 0v6m0-6h6m-6 0H6"
          />
        </svg>{" "}
        Написать комментарий
      </button>
    </>
  );
};

export default ViewAnnounce;
