import * as React from "react";

interface Props {
  setSearchQuery: any;
}

const SearchInput: React.FC<Props> = ({ setSearchQuery }) => {
  const [data, setData] = React.useState({
    search: "",
  });
  const handleChange = ({ target }: any) => {
    setData((prevState: any) => ({
      ...prevState,
      [target.name]: target.value,
    }));
  };
  const handleSubmit = (e: any) => {
    e.preventDefault();
    console.log("submit");
    setSearchQuery(data);
  };
  return (
    <>
      <form onSubmit={handleSubmit}>
        <label
          htmlFor="default-search"
          className="mb-2 text-sm font-medium text-gray-900 sr-only dark:text-white"
        >
          Поиск
        </label>

        <div className="relative">
          <div className="absolute inset-y-0 left-0 flex items-center pl-3 pointer-events-none">
            <svg
              aria-hidden="true"
              className="w-5 h-5 text-gray-500 dark:text-gray-400"
              fill="none"
              stroke="currentColor"
              viewBox="0 0 24 24"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                stroke-linecap="round"
                stroke-linejoin="round"
                stroke-width="2"
                d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z"
              ></path>
            </svg>
          </div>
          <input
            type="search"
            id="default-search"
            className="block w-full px-4 py-4 pl-10 text-sm md:text-lg outline-none shadow-md text-gray-900 border border-gray-300 rounded-sm bg-gray-50 focus:ring-blue-500 focus:border-blue-500 "
            placeholder="Ключевые слова"
            name="search"
            value={data.search}
            onChange={handleChange}
          />
          <button
            type="submit"
            className="text-white absolute right-2 bottom-2 bg-yellow-400 hover:bg-yellow-300 focus:ring-4 focus:outline-none font-normal md:font-medium rounded-sm text-sm md:text-md md:px-6 md:py-3 px-1 py-2.5"
          >
            Искать
          </button>
        </div>
      </form>
    </>
  );
};

export default SearchInput;
