import { useParams } from "react-router-dom";
import NavBar from "../common/NavBar";
import ViewAnnounce from "../common/ViewAnnounce";
import { useSelector } from "react-redux";
import { getAnnouncements, getRplies } from "../../store/announcement";
import AboutFooter from "../common/AboutFooter";

const ViewAnnouncePage = () => {
  const { id } = useParams();

  const announces = useSelector(getAnnouncements());
  const replies = useSelector(getRplies());
  console.log(id);
  return (
    <>
      <NavBar />

      <div className="w-4/6 mx-auto mt-14">
        <ViewAnnounce
          announce={announces.find((a: any) => a._id === id)}
          replies={replies.filter((r: any) => r.announce === id)}
        />
        <AboutFooter />
      </div>
    </>
  );
};

export default ViewAnnouncePage;
