import * as React from "react";
import { useAppDispatch } from "../../hooks";
import { createComment } from "../../store/announcement";

interface Props {
  showModal: string;
  setShowModal: any;
  announce: any;
}

type objectOptions = {
  [key: string]: string;
};

const starMock: objectOptions = {
  star1: "text-gray-300",
  star2: "text-gray-300",
  star3: "text-gray-300",
  star4: "text-gray-300",
  star5: "text-gray-300",
};

const ModalCreate: React.FC<Props> = ({
  showModal,
  setShowModal,
  announce,
}) => {
  const [stars, setStars] = React.useState(starMock);
  const [data, setData] = React.useState({
    announce: announce._id,
    userName: "",
    email: "",
    rate: 0,
    comment: "",
  });
  const dispatch = useAppDispatch();
  const handleStarClick = (index: number) => {
    if (stars[`star${index + 1}`] === "text-yellow-400") {
      setStars(starMock);
      setData((prevState) => ({ ...prevState, rate: 0 }));
      return "Done";
    }
    const starsUpdate: objectOptions = {};
    for (let i = 1; i <= index + 1; i++) {
      starsUpdate[`star${i}`] = "text-yellow-400";
    }
    setStars({
      ...starMock,
      ...starsUpdate,
    });
    setData((prevState) => ({ ...prevState, rate: index + 1 }));
  };
  const handleChange = ({ target }: any) => {
    setData((prevState) => ({ ...prevState, [target.name]: target.value }));
  };
  const handleSubmit = (e: any) => {
    e.preventDefault();
    dispatch(createComment(data));
    setShowModal(" hidden");
  };
  return (
    <div
      id="defaultModal"
      tabIndex={-1}
      className={
        "fixed left-0 top-0 z-50 w-full p-4 overflow-x-hidden overflow-y-hidden md:inset-0 h-full md:h-full bg-opacity-20 bg-black " +
        showModal
      }
    >
      <div className="relative mx-auto mt-40 w-full h-full max-w-2xl md:h-auto">
        <div className="relative bg-white rounded-lg shadow dark:bg-gray-700">
          <div className="flex items-start justify-between p-4 border-b rounded-t dark:border-gray-600">
            <h3 className="text-xl font-semibold text-gray-900 dark:text-white">
              Добавление комментария для {announce.name}
            </h3>
            <button
              type="button"
              onClick={() => setShowModal("hidden")}
              className="text-gray-400 bg-transparent hover:bg-gray-200 hover:text-gray-900 rounded-lg text-sm p-1.5 ml-auto inline-flex items-center dark:hover:bg-gray-600 dark:hover:text-white"
              data-modal-toggle="defaultModal"
            >
              <svg
                aria-hidden="true"
                className="w-5 h-5"
                fill="currentColor"
                viewBox="0 0 20 20"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path
                  fill-rule="evenodd"
                  d="M4.293 4.293a1 1 0 011.414 0L10 8.586l4.293-4.293a1 1 0 111.414 1.414L11.414 10l4.293 4.293a1 1 0 01-1.414 1.414L10 11.414l-4.293 4.293a1 1 0 01-1.414-1.414L8.586 10 4.293 5.707a1 1 0 010-1.414z"
                  clip-rule="evenodd"
                ></path>
              </svg>
              <span className="sr-only">Close modal</span>
            </button>
          </div>
          <div className="p-6 space-y-6">
            <form onSubmit={handleSubmit}>
              <div className="relative z-0 mb-4">
                <input
                  type="text"
                  id="floating_standard"
                  className="block py-2.5 px-0 w-full text-sm text-gray-900 bg-transparent border-0 border-b-2 border-gray-300 appearance-none dark:text-white dark:border-gray-600 dark:focus:border-blue-500 focus:outline-none focus:ring-0 focus:border-blue-600 peer"
                  placeholder=" "
                  name="userName"
                  value={data.userName}
                  onChange={handleChange}
                  required
                />
                <label
                  htmlFor="floating_standard"
                  className="absolute text-sm text-gray-500 dark:text-gray-400 duration-300 transform -translate-y-6 scale-75 top-3 -z-10 origin-[0] peer-focus:left-0 peer-focus:text-blue-600 peer-focus:dark:text-blue-500 peer-placeholder-shown:scale-100 peer-placeholder-shown:translate-y-0 peer-focus:scale-75 peer-focus:-translate-y-6"
                >
                  Имя
                </label>
              </div>
              <div className="relative z-0 mb-4">
                <input
                  type="text"
                  id="floating_standard"
                  className="block py-2.5 px-0 w-full text-sm text-gray-900 bg-transparent border-0 border-b-2 border-gray-300 appearance-none dark:text-white dark:border-gray-600 dark:focus:border-blue-500 focus:outline-none focus:ring-0 focus:border-blue-600 peer"
                  placeholder=" "
                  value={data.email}
                  name="email"
                  onChange={handleChange}
                  required
                />
                <label
                  htmlFor="floating_standard"
                  className="absolute text-sm text-gray-500 dark:text-gray-400 duration-300 transform -translate-y-6 scale-75 top-3 -z-10 origin-[0] peer-focus:left-0 peer-focus:text-blue-600 peer-focus:dark:text-blue-500 peer-placeholder-shown:scale-100 peer-placeholder-shown:translate-y-0 peer-focus:scale-75 peer-focus:-translate-y-6"
                >
                  Email
                </label>
              </div>
              <div className="flex mt-5 mb-5">
                <span className="mr-3 text-gray-400">Оценка:</span>
                {Object.values(stars).map((s, index) => (
                  <button type="button" onClick={() => handleStarClick(index)}>
                    <svg
                      aria-hidden="true"
                      className={"w-7 h-7 " + s}
                      fill="currentColor"
                      viewBox="0 0 20 20"
                      xmlns="http://www.w3.org/2000/svg"
                    >
                      <path d="M9.049 2.927c.3-.921 1.603-.921 1.902 0l1.07 3.292a1 1 0 00.95.69h3.462c.969 0 1.371 1.24.588 1.81l-2.8 2.034a1 1 0 00-.364 1.118l1.07 3.292c.3.921-.755 1.688-1.54 1.118l-2.8-2.034a1 1 0 00-1.175 0l-2.8 2.034c-.784.57-1.838-.197-1.539-1.118l1.07-3.292a1 1 0 00-.364-1.118L2.98 8.72c-.783-.57-.38-1.81.588-1.81h3.461a1 1 0 00.951-.69l1.07-3.292z"></path>
                    </svg>
                  </button>
                ))}
              </div>
              <div className="w-full mb-4 border border-gray-200 rounded-lg bg-gray-50 dark:bg-gray-700 dark:border-gray-600">
                <div className="px-4 py-2 bg-white rounded-t-lg dark:bg-gray-800">
                  <label htmlFor="comment" className="sr-only">
                    Комментарий
                  </label>
                  <textarea
                    id="comment"
                    value={data.comment}
                    onChange={handleChange}
                    name="comment"
                    rows={4}
                    className="w-full px-0 text-sm text-gray-900 bg-white border-0 dark:bg-gray-800 focus:ring-0 outline-none"
                    placeholder="Напишите отзыв..."
                    required
                  ></textarea>
                </div>
                <div className="flex items-center justify-between px-3 py-2 border-t dark:border-gray-600">
                  <button
                    type="submit"
                    className="pl-2 pr-3 py-2 bg-green-400 rounded-sm shadow-md text-white font-normal flex flex-row justify-center items-center"
                  >
                    Опубликовать
                  </button>
                </div>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  );
};

export default ModalCreate;
