import httpService from "./http.service.js";

const announcementEndpoint = "announcement/";

const announceService = {
  getItems: async () => {
    const { data } = await httpService.get(
      announcementEndpoint + "getAnnouncements/"
    );
    return data;
  },
  removeItem: async (announceId: string) => {
    const { data } = await httpService.post(
      announcementEndpoint + "removeAnnouncement/",
      { announceId }
    );
    return data;
  },
  removeReply: async (replyId: string) => {
    const { data } = await httpService.post(
      announcementEndpoint + "removeReply/",
      { replyId }
    );
    return data;
  },
  addItem: async (payload: object) => {
    const { data } = await httpService.post(
      announcementEndpoint + "createNew/",
      payload
    );
    return data;
  },
  addComment: async (payload: object) => {
    const { data } = await httpService.post(
      announcementEndpoint + "createComment/",
      payload
    );
    return data;
  },
};

export default announceService;
