import { useSelector } from "react-redux";
import AboutFooter from "../common/AboutFooter";
import NavBar from "../common/NavBar";
import SearchInput from "../common/SearchInput";
import Tabs from "../common/Tabs";
import * as React from "react";
import { getAnnouncements, getRplies } from "../../store/announcement";
// @ts-ignore
import _ from "lodash";

const IndexPage = () => {
  const [searchQuery, setSearchQuery] = React.useState();
  const announces = useSelector(getAnnouncements());
  const replies = useSelector(getRplies());
  React.useEffect(() => {
    // @ts-ignore
    setSearchQuery(_.orderBy(announces, ["createdAt"], ["desc", "asc"]));
  }, [announces]);
  function setQuery(data: any) {
    if (data.search.trim().length === 0) {
      // @ts-ignore
      setSearchQuery(_.orderBy(announces, ["createdAt"], ["desc", "asc"]));
    } else {
      const newArray = announces.filter((a: any) =>
        a.name.toLowerCase().trim().includes(data.search.toLowerCase().trim())
      );
      setSearchQuery(newArray);
    }
  }
  function filterSet(filter: any) {
    if (filter === "data") {
      // @ts-ignore
      setSearchQuery(_.orderBy(announces, ["createdAt"], ["desc", "asc"]));
    } else {
      if (filter === "rate") {
        // @ts-ignore
        setSearchQuery(_.orderBy(announces, ["rate"], ["desc", "asc"]));
      }
      if (filter === "popular") {
        // @ts-ignore
        setSearchQuery(_.orderBy(announces, ["items.length"], ["desc", "asc"]));
      }
    }
  }
  return (
    <>
      <NavBar />
      <div className="w-4/6 mx-auto mt-14">
        <SearchInput setSearchQuery={setQuery} />
        <Tabs announces={searchQuery} replies={replies} filterSet={filterSet} />
        <AboutFooter />
      </div>
    </>
  );
};

export default IndexPage;
