import { Link, useNavigate } from "react-router-dom";
import logo from "../../vendor/images/logo.png";

const NavBar = () => {
  const navigate = useNavigate();
  return (
    <div className="bg-white w-full px-10 flex justify-between items-center border-b border-gray-300">
      <div className="flex flex-col items-start py-2.5 hover:cursor-pointer">
        {/* <h1 className="text-2xl font-normal">Отзывы</h1>
        <p className="text-sm text-gray-500 font-normal">
          Честные отзывы о бизнесе
        </p> */}
        <img onClick={() => navigate("/")} src={logo} alt="" width={200} />
      </div>
      <div className="hidden md:flex flex-row items-center justify-center gap-x-14">
        <div className="flex gap-x-6">
          <Link to="/" className="text-md text-gray-500 font-normal">
            Главная
          </Link>
        </div>
        <div className="flex flex-row items-center justify-center">
          <button
            onClick={() => navigate("/create")}
            className="pl-2 pr-3 py-2 bg-green-400 rounded-sm shadow-md text-white font-normal flex flex-row justify-center items-center"
          >
            <svg
              className="w-6 h-6 mr-1"
              fill="none"
              stroke="currentColor"
              viewBox="0 0 24 24"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth={2}
                d="M12 6v6m0 0v6m0-6h6m-6 0H6"
              />
            </svg>{" "}
            Добавить объявление
          </button>
        </div>
      </div>
      <button className="md:hidden flex">
        <svg
          className="w-6 h-6"
          fill="none"
          stroke="currentColor"
          viewBox="0 0 24 24"
          xmlns="http://www.w3.org/2000/svg"
        >
          <path
            strokeLinecap="round"
            strokeLinejoin="round"
            strokeWidth={2}
            d="M4 6h16M4 12h16M4 18h16"
          />
        </svg>
      </button>
    </div>
  );
};

export default NavBar;
