import { Helmet } from "react-helmet-async";
import { useSelector } from "react-redux";
import { Link, useNavigate } from "react-router-dom";
import { getCurrentUserId } from "../../store/users";
import { useAppDispatch } from "../../hooks";
import { removeAnnounce } from "../../store/announcement";

interface Props {
  announce: any;
  replies: any;
}

const TabItem: React.FC<Props> = ({ announce, replies }) => {
  const currentUserId = useSelector(getCurrentUserId());
  const navigate = useNavigate();
  const dispatch = useAppDispatch();
  const getRate = () => {
    const rateList = replies.map((i: any) => i.rate);
    const sumRates = rateList.reduce((acc: any, i: any) => {
      return (acc += i);
    }, 0);
    return Math.floor(sumRates / rateList.length);
  };
  const renderStars = () => {
    const starList = [];
    for (let i = 1; i <= getRate(); i++) {
      starList.push("text-yellow-400");
    }
    for (let i = 1; i <= 5 - getRate(); i++) {
      starList.push("text-gray-300");
    }
    return starList;
  };
  return (
    <div className="relative z-0 w-96 lg:w-72 xl:w-72 2xl:w-80 px-7 py-5 border border-gray-100 shadow-md">
      <Link to={`/view/${announce._id}`} className="hover:cursor-pointer">
        <p className="text-2xl font-normal">{announce.name}</p>
        <p className="text-xs font-normal mt-2 text-gray-500">
          ДОБАВЛЕНО{" "}
          {new Date(announce.createdAt).toLocaleString().split(",")[0]}
        </p>
        <div className="flex items-center mt-1.5">
          {renderStars().map((s) => (
            <svg
              aria-hidden="true"
              className={"w-5 h-5 " + s}
              fill="currentColor"
              viewBox="0 0 20 20"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path d="M9.049 2.927c.3-.921 1.603-.921 1.902 0l1.07 3.292a1 1 0 00.95.69h3.462c.969 0 1.371 1.24.588 1.81l-2.8 2.034a1 1 0 00-.364 1.118l1.07 3.292c.3.921-.755 1.688-1.54 1.118l-2.8-2.034a1 1 0 00-1.175 0l-2.8 2.034c-.784.57-1.838-.197-1.539-1.118l1.07-3.292a1 1 0 00-.364-1.118L2.98 8.72c-.783-.57-.38-1.81.588-1.81h3.461a1 1 0 00.951-.69l1.07-3.292z"></path>
            </svg>
          ))}

          <p className="ml-1.5 text-md text-gray-800">
            {getRate()},0{" "}
            <span className="text-gray-400">({announce.items.length})</span>
          </p>
        </div>
      </Link>
      {currentUserId && (
        <button
          onClick={() => dispatch(removeAnnounce(announce._id))}
          className="mt-2 bg-red-500 px-5 py-1 rounded-md text-white"
        >
          Удалить
        </button>
      )}
    </div>
  );
};

export default TabItem;
