import { useSelector } from "react-redux";
import { getCurrentUserId } from "../../store/users";
import { useAppDispatch } from "../../hooks";
import { removeReply } from "../../store/announcement";

interface Props {
  stars: any;
  reply: any;
}

const ReplyComment: React.FC<Props> = ({ stars, reply }) => {
  const currentUserId = useSelector(getCurrentUserId());
  const dispatch = useAppDispatch();
  return (
    <div className="">
      <h1 className="text-md font-medium mt-2">
        {reply.userName}{" "}
        {currentUserId ? (
          <button
            onClick={() => dispatch(removeReply(reply._id))}
            className="ml-2 bg-red-500 px-4 py-0 text-sm rounded-sm text-white"
          >
            Удалить
          </button>
        ) : null}
      </h1>
      <div className="flex mt-2 items-center flex-row">
        {stars.map((s: any) => (
          <svg
            aria-hidden="true"
            className={"w-5 h-5 " + s}
            fill="currentColor"
            viewBox="0 0 20 20"
            xmlns="http://www.w3.org/2000/svg"
          >
            <path d="M9.049 2.927c.3-.921 1.603-.921 1.902 0l1.07 3.292a1 1 0 00.95.69h3.462c.969 0 1.371 1.24.588 1.81l-2.8 2.034a1 1 0 00-.364 1.118l1.07 3.292c.3.921-.755 1.688-1.54 1.118l-2.8-2.034a1 1 0 00-1.175 0l-2.8 2.034c-.784.57-1.838-.197-1.539-1.118l1.07-3.292a1 1 0 00-.364-1.118L2.98 8.72c-.783-.57-.38-1.81.588-1.81h3.461a1 1 0 00.951-.69l1.07-3.292z"></path>
          </svg>
        ))}
        <p className="text-xs font-medium ml-3 text-gray-400">
          ДОБАВЛЕНО {new Date(reply.createdAt).toLocaleString().split(",")[0]}
        </p>
      </div>
      <p className="mt-2 text-gray-500 font-normal whitespace-pre-line">
        {reply.comment}
      </p>
    </div>
  );
};

export default ReplyComment;
