import { createSlice } from "@reduxjs/toolkit";
import announceService from "../service/announcement.service";

const announceSlice = createSlice({
  name: "announcement",
  initialState: {
    entities: null,
    isLoading: true,
    error: null,
  },
  reducers: {
    announcementRequested: (state) => {
      state.isLoading = true;
    },
    announcementRecieved: (state, action) => {
      state.isLoading = false;
      state.entities = action.payload;
    },
    announcementRequestFailed: (state, action) => {
      state.isLoading = false;
      state.error = action.payload;
    },
    announcementAdded: (state, action) => {
      state.isLoading = false;

      Array.isArray(state.entities.announcements)
        ? (state.entities.announcements = [
            ...state.entities.announcements,
            action.payload,
          ])
        : (state.entities.announcements = [action.payload]);
    },
  },
});

const { actions, reducer: announceReducer } = announceSlice;
const {
  announcementRequested,
  announcementRecieved,
  announcementRequestFailed,
  announcementAdded,
} = actions;

export const loadAnnouncements = () => async (dispatch) => {
  dispatch(announcementRequested());
  try {
    const content = await announceService.getItems();
    dispatch(announcementRecieved(content));
  } catch (e) {
    dispatch(announcementRequestFailed(e.message));
  }
};

export const addAnnounce = (payload) => async (dispatch) => {
  try {
    const oldData = await announceService.addItem(payload);
    const content = await announceService.getItems();
    dispatch(announcementRecieved(content));
  } catch (e) {
    dispatch(announcementRequestFailed(e.message));
  }
};

export const removeAnnounce = (announceId) => async (dispatch) => {
  try {
    const data = await announceService.removeItem(announceId);
    const content = await announceService.getItems();
    dispatch(announcementRecieved(content));
  } catch (e) {
    dispatch(announcementRequestFailed(e.message));
  }
};

export const removeReply = (replyId) => async (dispatch) => {
  try {
    const data = await announceService.removeReply(replyId);
    const content = await announceService.getItems();
    dispatch(announcementRecieved(content));
  } catch (e) {
    dispatch(announcementRequestFailed(e.message));
  }
};

export const createComment = (payload) => async (dispatch) => {
  try {
    const oldData = await announceService.addComment(payload);
    const content = await announceService.getItems();
    dispatch(announcementRecieved(content));
  } catch (e) {
    dispatch(announcementRequestFailed(e.message));
  }
};

export const getAnnouncements = () => (state) =>
  state.announce.entities.announcements;
export const getRplies = () => (state) => state.announce.entities.replies;
export const getAnnounceIsLoading = () => (state) => state.announce.isLoading;

export default announceReducer;
