import * as React from "react";
import TabItem from "./TabItem";

interface Props {
  announces: any;
  replies: any;
  filterSet: any;
}

const Tabs: React.FC<Props> = ({ announces, replies, filterSet }) => {
  const [dropDown, setDropdown] = React.useState(" hidden");
  const [filterName, setFilerName] = React.useState("ДАТА");
  if (!announces) {
    return <>"Loading..."</>;
  }
  return (
    <div className="mt-10">
      <div className="flex justify-between text-gray-500 text-xs gap-y-2 flex-col md:flex-row">
        <p>ОТОБРАЖАЮТСЯ 1-2 ИЗ 2 РЕЗУЛЬТАТОВ</p>
        <div className="flex relative items-center gap-x-2">
          <p>СОРТИРОВАТЬ ПО</p>
          <button
            onClick={() => setDropdown(dropDown === " hidden" ? "" : " hidden")}
            className="z-20 flex relative justify-center items-center border border-gray-400 text-gray-500 px-2 py-1 gap-x-3 w-28"
          >
            <span>{filterName}</span>{" "}
            <svg
              className="w-4 h-4"
              fill="none"
              stroke="currentColor"
              viewBox="0 0 24 24"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth={2}
                d="M19 9l-7 7-7-7"
              />
            </svg>
            <div
              className={
                "absolute w-fit z-20 bg-white  right-0 top-6   text-gray-500 border h-7" +
                dropDown
              }
            >
              <ul className="border border-gray-400 px-0.5 relative z-20">
                <li
                  onClick={() => {
                    filterSet("date");
                    setFilerName("ДАТА");
                  }}
                  className="py-1 px-1.5 border-b border-gray-400"
                >
                  ДАТА
                </li>
                <li
                  onClick={() => {
                    filterSet("rate");
                    setFilerName("РЕЙТИНГ");
                  }}
                  className="py-1 px-1.5 border-b border-gray-400"
                >
                  РЕЙТИНГ
                </li>
                <li
                  onClick={() => {
                    filterSet("popular");
                    setFilerName("ОТЗЫВЫ");
                  }}
                  className="py-1.5 px-1"
                >
                  ПОПУЛЯРНОСТЬ
                </li>
              </ul>
            </div>
          </button>
        </div>
      </div>
      <div
        className={
          "flex flex-row justify-between flex-wrap gap-y-10 md:mt-4 " +
          (dropDown !== " hidden" ? "mt-16" : "mt-4")
        }
      >
        {announces.map((a: any) => (
          <TabItem
            key={a._id}
            announce={a}
            replies={replies.filter((r: any) => r.announce === a._id)}
          />
        ))}
      </div>
    </div>
  );
};

export default Tabs;
